<template>
  <div class="container row">
    <div class="col-3 offset-6">
      <div @keyup.enter="updateTutor()">
        <h2 class="text-center my-3">Tutorial</h2>
        <div class="my-3">
          <label for="title" class="d-block">Title</label>
          <input type="text" id="title" name="title" class="form-control" v-model="title">
        </div>
        <div class="mb-3">
          <label for="description" class="d-block">Description</label>
          <input type="text" id="description" name="description" class="form-control" v-model="description">
        </div>
        <p>status:
          <span v-if="status">
            published
          </span>
          <span v-else>
            unPublished
          </span>
        </p>
      </div>
      <div class="btn-group container px-0 mt-4">
        <button @click="status = !status" class="btn btn-outline-info">Publish</button>
        <button @click="deleteTutor()" class="btn btn-outline-danger">Delete</button>
        <button @click="updateTutor()" class="btn btn-outline-info">Update</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tutorial-details',
  props: ['database'],
  data () {
    return {
      title: null,
      description: null,
      status: null,
      tutorId: null
    }
  },
  methods: {
    async deleteTutor () {
      const resolve = await this.database.delete(this.tutorId)
      this.$router.push({ path: '/' })
      setTimeout(() => {
        this.message(resolve.data)
      }, 300)
    },
    async updateTutor () {
      const resolve = await this.database.update(this.tutorId, {
        title: this.title,
        description: this.description,
        published: this.status
      })
      this.$router.push({ path: '/' })
      setTimeout(() => {
        this.message(resolve.data)
      }, 300)
    },
    message (msg) {
      alert(msg)
    }
  },
  async mounted () {
    const { id } = this.$route.params
    const num = parseInt(id)
    this.tutorId = num
    const resolve = await this.database.findById(num)
    const { title, description, published } = resolve.data
    this.title = title
    this.description = description
    this.status = published
  }
}
</script>
