<template>
    <div class="container row mt-5">
      <div class="col-3 offset-6">
        <h2 class="text-center">Tutorial</h2>
        <div class="my-3">
          <label for="title"  class="d-block">Title</label>
          <input type="text" id="title"  class="form-control" v-model="title">
        </div>
        <div class="my-3">
          <label for="description" class="d-block">Description</label>
          <input type="text" id="description" class="form-control"  v-model="description">
        </div>
          <button @click="createTutor()" class="mt-3 btn btn-outline-info container">Submit</button>
      </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      title: null,
      description: null
    }
  },
  props: ['database'],
  methods: {
    async createTutor () {
      await this.database.create({
        title: this.title,
        description: this.description
      })
      this.$router.push({ path: '/' })
    }
  }
}
</script>
