<template>
  <div @keyup.enter="findByTitle()" class=" container">
    <div class="d-flex justify-content-center">
      <input type="text" name="title" id="search" v-model="title" class="form-control">
      <button @click="findByTitle()" class="btn btn-primary ">Search</button>
    </div>
  </div>
  <div class="container-fluid row mt-5 mx-0">
    <div class="col-7 d-flex justify-content-end pe-0">
      <div class="" id="listTutors">
        <h2 class="text-center mb-3">Tutorials List</h2>
        <ul class="list-group">
          <li v-for="(tutor, id) in allTutorials"
            :key="id"
            @click="detailTutor(tutor)"
            class="list-group-item text-center">
            {{tutor.title}}
          </li>
        </ul>
        <button @click="deleteAll()" class="mt-3 btn btn-danger container">Remove All</button>
      </div>
    </div>
    <div class="col-5 d-flex justify-content-center ps-0">
      <div id="listTutors" v-if="items.length > 0">
        <h2 class="text-center mb-3">Tutorial</h2>
          <div v-for="(item, index) in items" :key="index">
            <ul  class="list-group">
              <li class="list-group-item">
                title: {{item.title}}
              </li>
              <li class="list-group-item">
                description: {{item.description}}
              </li>
              <li class="list-group-item">
                published: {{item.published}}
              </li>
            </ul>
            <button @click="changeRouter(item.id)" class="mt-3 btn btn-danger container">Edit</button>
          </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'all-tutorials',
  data () {
    return {
      allTutorials: [],
      items: [],
      title: null
    }
  },
  props: ['database'],
  methods: {
    retrieveTutorials: async function () {
      const resolve = await this.database.getAll()
      this.allTutorials = resolve.data
    },
    detailTutor (tutor) {
      this.items[0] = tutor
    },
    async deleteAll () {
      const resolve = await this.database.deleteAll()
      this.allTutorials = []
      setTimeout(() => {
        this.message(resolve.data)
      }, 300)
    },
    async findByTitle () {
      if (this.title === null) return this.message('sorry! you need to fill in the title')
      const resolve = await this.database.getAll(`/?title=${this.title}`)
      this.title = null
      if (resolve.data.length === 0) return this.message('not found tutorial')
      else this.items = resolve.data
    },
    message (msg) {
      alert(msg)
    },
    changeRouter (id) {
      this.$router.push({ path: `/tutorials/${id}` })
    }
  },
  mounted () {
    this.retrieveTutorials()
  }
}
</script>
